import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// form
import { useForm} from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Typography } from '@mui/material';
// swal
import swal from 'sweetalert';
// @mui
import { styled } from '@mui/material/styles';

// utils
import { fData } from '../../utils/formatNumber';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// _mock
import { UserType } from '../../_mock';
// components
import Label from '../../components/Label';
import { FormProvider, RHFSelect, RHFSwitch, RHFTextField, RHFUploadAvatar, RHFRadioGroup, } from '../../components/hook-form';

import axios from '../../utils/axios';
// ----------------------------------------------------------------------

UserNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentUser: PropTypes.object,
};
const LabelStyle = styled(Typography)(({ theme }) => ({
    ...theme.typography.subtitle2,
    color: theme.palette.text.secondary,
    marginBottom: theme.spacing(1),
  }));
export default function UserNewForm({ isEdit, currentUser }) {
//   const navigate = useNavigate();

  const { enqueueSnackbar } = useSnackbar();
  const [updatekey, Setkey] = useState(false);

  const NewUserSchema = Yup.object().shape({
    name: Yup.string().required('Full Name is required'),
    username: Yup.string().required('Username Required'),
    email: Yup.string().required('Email is required').email(),
    phone: Yup.string().required('Phone number is required'),
    type: Yup.string().required('Account Role Required'),
    status: Yup.string().required('Account Status Required'),
  });
  const STATUS_OPTION = ['Active','Deactivate','Banned','Unverified'];

  const defaultValues = useMemo(
    () => ({
      name: currentUser?.name || '',
      username: currentUser?.username || '',
      email: currentUser?.email || '',
      phone: currentUser?.phone || '',
      address: currentUser?.address || '',
      user_limit: currentUser?.user_limit || '',
      type: currentUser?.type || '',
      autofund: currentUser?.autofund || '',
      pin: currentUser?.pin || '',
      profile_image: currentUser?.profile_image || '',
      about: currentUser?.about || '',
      status: currentUser?.status || '',
      reason: currentUser?.reason || '',
      webhook: currentUser?.webhook || '',
      otp: currentUser?.otp || '',
      kyc: currentUser?.kyc || false,
      sterlen: currentUser?.sterlen || '',
      rolex: currentUser?.rolex || '',
      fed: currentUser?.fed || '',
      wema: currentUser?.wema || '',
      apikey: currentUser?.apikey || '',
      ref: currentUser?.ref || '',
      bal: currentUser?.bal || '',
      refbal: currentUser?.bal || '',
      isVerified: false
    }),
    [currentUser]
  );
  const AccessToken =  window.localStorage.getItem('accessToken');
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    // defaultValues,
  });

  const {
    reset,
    watch,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

 
  const values = watch();
  useEffect(() => {
    if (isEdit && currentUser !== undefined) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUser]);

  const onSubmit = async (data) => {
    // eslint-disable-next-line prefer-const
    let formData = new FormData();
        // eslint-disable-next-line no-restricted-syntax, guard-for-in
        for (const key in data) {
            formData.append(key, data[key]);
        }
     try{
    await axios.post(`/api/system/admin/create/new/user/safe/url/${AccessToken}/secure`,formData,{
        headers: {
            'Content-Type': 'multipart/form-data'
          }
    });
     enqueueSnackbar('Create success!');
     navigate(PATH_ADMIN.user.all_user);
     }catch(error){
        if(error.data?.message !== undefined){
            enqueueSnackbar(error.data?.message, { variant: 'error' })  
             } else if(error?.message !== undefined){
                enqueueSnackbar(error?.message, { variant: 'error' })   
             }else{
                enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
             }
     }
  }

  const onUpdateKey = async (user) => {
    swal({
      title: "INFO",
      text: `Are Sure You Want To Change '${  user}' Apikey`,
      icon: "info",
      buttons: true,
      dangerMode: true,
    })
    .then((willDelete) => {
      if (willDelete) {
        Setkey(true);
         axios.post(`/api/system/admin/change_key/changes/of/key/url/${AccessToken}/secure`,{
         username: user
      }).then(() => swal('success', `${user} Api Key Changed Successfuly`, 'success'), Setkey(false))
      } else {
        swal("Request Cancelled");
      }
    });
  }

  const onCreateReset = async (data) => {
// eslint-disable-next-line prefer-const
let formData = new FormData();
// eslint-disable-next-line no-restricted-syntax, guard-for-in
for (const key in data) {
    formData.append(key, data[key]);
}
try{
await axios.post(`/api/system/admin/create/new/user/safe/url/${AccessToken}/secure`,formData,{
headers: {
    'Content-Type': 'multipart/form-data'
  }
})
reset();
enqueueSnackbar('Create success!');
}catch(error){
if(error.data?.message !== undefined){
    enqueueSnackbar(error.data?.message, { variant: 'error' })  
     } else if(error?.message !== undefined){
        enqueueSnackbar(error?.message, { variant: 'error' })   
     }else{
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
     }
}
  }

  const onEdit = async(data)=>{
    // eslint-disable-next-line prefer-const
let formData = new FormData();
// eslint-disable-next-line no-restricted-syntax, guard-for-in
for (const key in data) {
    formData.append(key, data[key]);
}
formData.append('user_id', currentUser?.id);
try{
await axios.post(`/api/system/admin/edit/edituser/adex/adex/secure/boss/asd/asd/changes/of/key/url/${AccessToken}/secure`,formData,{
headers: {
    'Content-Type': 'multipart/form-data'
  }
})
enqueueSnackbar('Update success!');
}catch(error){
if(error.data?.message !== undefined){
    enqueueSnackbar(error.data?.message, { variant: 'error' })  
     } else if(error?.message !== undefined){
        enqueueSnackbar(error?.message, { variant: 'error' })   
     }else{
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
     }
}
  }
  const handleDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setValue(
          'profile_image',
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          }),
        );
      }
    },
    [setValue]
  );

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(!isEdit? onSubmit : onEdit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={4}>
          <Card sx={{ py: 10, px: 3 }}>
            {isEdit && (
              <Label
               
                color={values.status !== 1? 'error' : 'success'}
                sx={{ textTransform: 'uppercase', position: 'absolute', top: 24, right: 24 }}
              >
                {values.status === 1 && 'active' || values.status === 'Active' && 'active' || values.status === 'Unverified' && 'unverified' || values.status === 0 && 'unverified' || values.status === 2  && 'banned' || values.status === 'Banned' && 'banned' || values.status === 3 && 'deactivated' || values.status === 'Deactivate' && 'deactivated'}
              </Label>
            )}
     
            <Box sx={{ mb: 5 }}>
              <RHFUploadAvatar
                name="profile_image"
                accept="image/*"
                maxSize={3145728}
                onDrop={handleDrop}
                helperText={
                  <Typography
                    variant="caption"
                    sx={{
                      mt: 2,
                      mx: 'auto',
                      display: 'block',
                      textAlign: 'center',
                      color: 'text.secondary',
                    }}
                  >
                    Allowed *.jpeg, *.jpg, *.png, *.gif
                    <br /> max size of {fData(3145728)}
                  </Typography>
                }
              />
            </Box>
            
            <div>
                  <LabelStyle>status</LabelStyle>
                  <RHFRadioGroup
                    name="status"
                    options={STATUS_OPTION}
                    sx={{
                      '& .MuiFormControlLabel-root': { mr: 4 },
                    }}
                  />
                </div>
               
            <RHFSwitch
              name="isVerified"
              labelPlacement="start"
              label={
                <>
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                    Email Verified
                  </Typography>
                  <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                    Disabling this will automatically send the user a verification email
                  </Typography>
                </>
              }
              sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
            />

              <RHFSwitch
              name="kyc"
              def={values.kyc === 1}
              labelPlacement="start"
              label={
                <>
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                   KYC verification
                  </Typography>
                  <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                    Disabling this means this user account has not been verified
                  </Typography>

                </>
              }
             
              sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
            />
          </Card>
        </Grid>

        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="name" label="Full Name" defaultValue/>
              <RHFTextField name="username" label="Username" defaultValue disabled ={isEdit}/>
              <RHFTextField name="email" label="Email Address" defaultValue/>
              <RHFTextField name="phone" label="Phone Number" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
              <RHFSelect name="type" label="User Role" defaultValue="SMART">
                <option value="" />
                {UserType.map((option) => (
                  <option key={option?.code} value={option?.label}>
                    {option?.label}
                  </option>
                ))}
              </RHFSelect>

              <RHFTextField name="address" label="Address" defaultValue/>
              <RHFTextField  multiline rows={4} name="about" label="About User" defaultValue/>
              <RHFTextField name="webhook" label="Webhook"  defaultValue/>
              <RHFTextField name="pin" label="Transaction Pin"  defaultValue="0" type="number"  inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
             {!isEdit &&  <RHFTextField name="password" label="Password" />}
             {isEdit &&  <RHFTextField multiline rows={4} name="reason" label="Reason" defaultValue/>}
             {isEdit && <RHFTextField name="user_limit" label="Daily User Limit" type="number" defaultValue="0"/>}
       
             {isEdit && <RHFTextField  name="apikey" label="Api Key"  defaultValue disabled/>}
             {isEdit && <RHFTextField name="bal" label="Wallet Balance" defaultValue disabled/>}
             {isEdit && <RHFTextField name="refbal" label="Earning Wallet Balance" defaultValue disabled/>}
             {isEdit && <RHFTextField name="ref" label="Referral Username" defaultValue/>}
             {isEdit && <RHFTextField name="otp" label="OTP" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} defaultValue="0"/>}
             {isEdit && <RHFTextField name="sterlen" label="Sterling Bank" defaultValue = "0" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>}
             {isEdit && <RHFTextField name="wema" label="Wema Bank" defaultValue = "0" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>}
             {isEdit && <RHFTextField name="rolex" label="Monnipoint Bank" defaultValue = "0" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>}
             {isEdit && <RHFTextField name="fed" label="Fidelity Bank" defaultValue = "0" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>}
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 2, display: 'grid',
                columnGap: 1,
                rowGap: 2,  gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },}}>

              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {!isEdit ? 'Create User then Return User List' : 'Save Changes'}
              </LoadingButton>
              {!isEdit && <LoadingButton type="button" onClick={handleSubmit(onCreateReset)} variant="contained" color="warning" loading={isSubmitting}>
                Create then Reset
              </LoadingButton>}
              {isEdit && <LoadingButton type="button" onClick={()=>onUpdateKey(values?.username)} variant="contained" color="warning" loading={updatekey}>
                Update ApiKey
              </LoadingButton>}
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}